export const myMixin = {
   methods: {
    routerJump() {
      this.$router.push({
        name:'Article',
        params:{
          title:this.articleInfo.title,
          id: this.articleInfo.id,
        }
      })
    }
  },
  props: {
    articleInfo: {
      type: Object,
      default: {imgSrc:
          "https://cdn2.hubspot.net/hubfs/322787/Mychefcom/images/BLOG/Header-Blog/photo-culinaire-pexels.jpg",
        title: "默认的模板标题",
        summary:
          "太辛苦了，休息一下吧~~~",
        time: "2000-01-01 00:00:00",
        tags: ['JS','春招','笑话','哈哈哈哈法撒旦', '达到']}
    },
  },
  data() {
    return {
      day: '',
      month: '',
      MONTHS:['JANUARY','FEBUARY','MARCH','APRIL','MAY','JUNE','JULY','AUGUST','SEPTEMBER','OCTOBER','NOVEMBER','DECEMBER'],
      content: null
    }
  },
  mounted () {
    var time = this.$moment(this.articleInfo.time);
    this.day = time.date();
    this.month = this.MONTHS[time.month()];
    this.content = this.articleInfo.content.replace(/(#|`|\[TOC\]|\*)/g,'');
  },
}