<template>
  <div>
    <div class="container" v-if="$store.state.app.device === 'mobile'">
      <article-card-mobile  v-for="(item, index) in lists" :key="index" :articleInfo="item"></article-card-mobile>
    </div>
    <div class="container" v-else>
      <article-card v-for="(item, index) in lists" :key="index" :articleInfo="item"></article-card>
    </div>
  </div>
</template>

<script>
import articleCard from "@/components/articleCard/articleCard";
import articleCardMobile from "@/components/articleCard/articleCardMobile";
export default {
  components: {
    articleCard,
    articleCardMobile,
  },
  data() {
    return {
      lists: []
    }
  },
  mounted() {
    this.$axios
      .get("/article")
      .then((res) => {
        // console.log(res);
        this.lists = res.data.data;
      })
      .catch((error) => {
        console.log("error", error);
      });
  },
};
</script>

<style lang="scss" scoped>
.container {
  background-color: $body-background-color;
  margin-top: -10px;
}
</style>